import React from "react"
import { Link } from "gatsby"

import { rhythm, scale } from "../utils/typography"
import { RemarkCreatorPlugin } from 'gatsby-tinacms-remark'
import { withPlugin } from "tinacms"
import { useCMS } from "tinacms"

const Layout = ({ location, title, children }) => {
    const cms = useCMS()
    React.useEffect(() => {
        import("react-tinacms-date").then(({ DateFieldPlugin }) => {
            cms.plugins.add(DateFieldPlugin)
        })
    }, [])

  const rootPath = `${__PATH_PREFIX__}/`
  let header

  if (location.pathname === rootPath) {
    header = (
      <h1
        style={{
          ...scale(1.5),
          marginBottom: rhythm(1.5),
          marginTop: 0,
        }}
      >
        <Link
          style={{
            boxShadow: `none`,
            color: `inherit`,
          }}
          to={`/`}
        >
          {title}
        </Link>
      </h1>
    )
  } else {
    header = (
      <h3
        style={{
          fontFamily: `Montserrat, sans-serif`,
          marginTop: 0,
        }}
      >
        <Link
          style={{
            boxShadow: `none`,
            color: `inherit`,
          }}
          to={`/`}
        >
          {title}
        </Link>
      </h3>
    )
  }
  return (
    <div
      style={{
        marginLeft: `auto`,
        marginRight: `auto`,
        maxWidth: rhythm(24),
        padding: `${rhythm(1.5)} ${rhythm(3 / 4)}`,
      }}
    >
      <header>{header}</header>
      <main>{children}</main>
      <footer>
         {new Date().getFullYear()}, Built with
        {` `}
        <a href="https://www.gatsbyjs.org">Gatsby</a>
      </footer>
    </div>
  )
}

const CreatePostPlugin = new RemarkCreatorPlugin({
    label: "New Blog Post",
    filename({ title }) {
        return `content/blog/${title.replace(/\s+/g, "-").toLowerCase()}/index.md`
    },
    frontmatter({ title, date, author }) {
        // Asynchronously generate front matter by fetching data from some server.
        return new Promise(resolve => {
            setTimeout(() => {
                resolve({
                    title,
                    date: date || new Date(),
                    heading_color: "#e6faf8",
                    description: "A human friendy summary",
                    author: author,
                    thumbnail: "../../assets/profile-pic.jpg",
                })
            }, 1000)
        })
    },
    body({ title }) {
        return `# ${title}`
    },
    fields: [
        {
            name: 'title',
            component: 'text',
            label: 'Title',
            description: 'The title of your new post',
            required: true
        },
        {
            name: 'date',
            component: 'date',
            label: 'Date',
            description: 'The default will be today'
        },
        {
            name: 'author',
            component: 'text',
            label: 'Author',
            description: 'Who wrote this?',
        }
    ],
})

export default withPlugin(Layout, CreatePostPlugin)
